import { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Grid, IconButton } from "@material-ui/core";
import Card from "./Card";
import { RadioStation } from "./DataTypes";
import axios from "axios";
import CardHeader from "./CardHeader";
import CardFooter from "./CardFooter";
import CardBody from "./CardBody";
import BackArrowImg from "../assets/img/back-arrow.png";
import SwitchOffImg from "../assets/img/switch.png";
import RadioStationRow from "./RadioStationRow";

const radiosApiUrl = process.env.REACT_APP_API_RADIOS as string;

const useStyle = makeStyles({
  root: {
    display: "flex",
    alignItems: "center",
    justifyItems: "center",
  },
  cardContainer: {
    width: "370px",
    margin: "50px auto",
  },
  radioListContainer: {
    height: "390px",
    overflowY: "scroll",
    scrollbarWidth: "none",
    padding: "0px 20px",
    borderBottom: "2px solid #3C3C48",
  },
  headerContainer: {
    height: "65px",
  },
  headerTitle: {
    fontSize: "1.8em",
    fontWeight: "bold",
    margin: "auto",
  },
  footerContainer: {
    height: "60px",
  },
  footerRadioTitle: {
    fontSize: "1.7em",
    margin: "0px auto",
    padding: "0px",
    width: "100%",
    textAlign: "center",
  },
  currentlyPlayingText: {
    color: "#D8A567",
    fontSize: "0.95em",
    margin: "0px auto",
    padding: "0px",
    width: "100%",
    textAlign: "center",
  },
});

export default function RadioWidget() {
  const classes = useStyle();
  const [radioStations, setRadioStations] = useState<RadioStation[]>([]);
  const [currentlyPlayingStationId, setCurrentlyPlayingStationId] =
    useState(-1);
  const [currentlyPlayingStation, setCurrentlyPlayingStation] =
    useState<RadioStation | null>(null);

  useEffect(() => {
    axios.get<RadioStation[]>(radiosApiUrl).then((radiosList) => {
      if (radiosList.status !== 200) {
        console.error("Error fetching the radio lists from the API");
      }
      setRadioStations(radiosList.data);
    });
  }, []);

  const handleClickRadioItem = (stationId: number) => {
    if (stationId === currentlyPlayingStationId) {
      setCurrentlyPlayingStationId(-1);
    } else {
      setCurrentlyPlayingStationId(stationId);
      setCurrentlyPlayingStation(
        radioStations.find((station) => station.id === stationId) || null
      );
    }
  };

  return (
    <div className={classes.root}>
      <div className={classes.cardContainer}>
        <Card>
          <CardHeader>
            <div className={classes.headerContainer}>
              <Grid container alignItems="center" justify="space-between">
                <Grid item>
                  <IconButton>
                    <img
                      height="35px"
                      src={BackArrowImg}
                      alt="back arrow button icon"
                    />
                  </IconButton>
                </Grid>
                <Grid item>
                  <div className={classes.headerTitle}>STATIONS</div>
                </Grid>
                <Grid item>
                  <IconButton>
                    <img
                      height="35px"
                      src={SwitchOffImg}
                      alt="switch off button icon"
                    />
                  </IconButton>
                </Grid>
              </Grid>
            </div>
          </CardHeader>

          <CardBody>
            <div
              className={classes.radioListContainer + " radio-list-scrollable"}
            >
              {radioStations.map((radio, index) => (
                <RadioStationRow
                  key={radio.id}
                  station={radio}
                  isPlaying={currentlyPlayingStationId === radio.id}
                  hasBottomBorder={index < radioStations.length - 1}
                  onClick={handleClickRadioItem}
                />
              ))}
            </div>
          </CardBody>

          <CardFooter>
            <div className={classes.footerContainer}>
              {currentlyPlayingStationId !== -1 && (
                <Grid container alignItems="center" justify="space-between">
                  <Grid item></Grid>
                  <Grid item>
                    <div>
                      <h6 className={classes.currentlyPlayingText}>
                        CURRENTLY PLAYING
                      </h6>
                      <div className={classes.footerRadioTitle}>
                        {currentlyPlayingStation?.name}
                      </div>
                    </div>
                  </Grid>
                  <Grid item></Grid>
                </Grid>
              )}
            </div>
          </CardFooter>
        </Card>
      </div>
    </div>
  );
}
