import { Collapse, Grid, IconButton, makeStyles } from "@material-ui/core";
import { RadioStation } from "./DataTypes";
import PlusIconImage from "../assets/img/plus.png";
import MinusIconImage from "../assets/img/minus.png";

const useStyle = makeStyles({
  radioItemRow: {
    height: "65px",
    paddingTop: "15px",
    paddingBottom: "15px",
    marginTop: "10px",
    cursor: "pointer",
  },
  radioItemExpandedRow: {
    height: "170px",
    paddingTop: "15px",
    paddingBottom: "15px",
    marginTop: "10px",
  },
  radioTitle: {
    fontSize: "1.7em",
  },
  radioFrequencyText: {
    fontSize: "1.7em",
    fontWeight: "bold",
  },
  radioLogo: {
    height: "150px",
    width: "150px",
    objectFit: "cover",
    borderRadius: "5000px",
    border: "3px solid #A0A8BD",
    cursor: "pointer",
  },
  iconButton: {
    margin: "0px",
    padding: "0px",
  },
});

export default function RadioStationRow({
  station,
  hasBottomBorder,
  onClick,
  isPlaying,
}: {
  station: RadioStation;
  isPlaying: boolean;
  hasBottomBorder: boolean;
  onClick: (radioId: number) => void;
}) {
  const classes = useStyle();

  return (
    <div>
      <Collapse in={isPlaying} collapsedHeight={0}>
        <div className={classes.radioItemExpandedRow}>
          <Grid container alignItems="center" justify="space-between">
            <Grid item>
              <IconButton
                classes={{
                  root: classes.iconButton,
                }}
                onClick={(e) => e.stopPropagation()}
              >
                <img height="35px" src={MinusIconImage} alt="Volume down" />
              </IconButton>
            </Grid>

            <Grid item>
              <IconButton
                classes={{
                  root: classes.iconButton,
                }}
                onClick={(e) => {
                  onClick(station.id);
                  e.stopPropagation();
                }}
              >
                <img
                  src={station.logo}
                  alt="Radio station logo"
                  className={classes.radioLogo}
                />
              </IconButton>
            </Grid>

            <Grid item>
              <IconButton
                classes={{
                  root: classes.iconButton,
                }}
                onClick={(e) => e.stopPropagation()}
              >
                <img height="35px" src={PlusIconImage} alt="Volume Up" />
              </IconButton>
            </Grid>
          </Grid>
        </div>
      </Collapse>

      <div
        style={{
          borderBottom: hasBottomBorder ? "2px solid #40404C" : undefined,
        }}
        className={classes.radioItemRow}
        onClick={(e) => onClick(station.id)}
      >
        <Grid container alignItems="center" justify="space-between">
          <Grid item>
            <span className={classes.radioTitle}>{station.name}</span>
          </Grid>

          <Grid item>
            <span className={classes.radioFrequencyText}>
              {station.frequency}
            </span>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
